
import { Component, OnInit, Input, forwardRef, EventEmitter, Output } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR , FormControl } from '@angular/forms';
import { DateAdapter , MAT_DATE_FORMATS } from '@angular/material/core';
import { MomentDateAdapter} from '@angular/material-moment-adapter';
import * as moment_ from 'moment';
const moment = moment_;

export const MY_FORMATS = {
    parse: {
      dateInput: 'LL',
    },
    display: {
      dateInput: 'LL',
      monthYearLabel: 'MMM YYYY',
      dateA11yLabel: 'LL',
      monthYearA11yLabel: 'MMMM YYYY',
    },
  };

@Component({
    selector: 'ngx-date-time-picker',
    template: `<div class="row">
  <div class="col-sm-8 col-md-8 col-lg-8 col-xs-12">
    <div class="row">
        <div class="col-sm-9 col-md-9 col-lg-9 col-xs-12">
            <div class="input-group">
                   
                    <input matInput [matDatepicker]="picker" class="form-control" [(ngModel)]="selectedDate" placeholder="Choose a date" 
                    (ngModelChange)="onDateSelect($event)">
                    <mat-datepicker #picker touchUi="true" disabled="false"></mat-datepicker>
                    <div class="input-group-btn">
                        <button class="btn btn-default" (click)="picker.open()">
                            <i class="glyphicon glyphicon-calendar"></i>
                          </button>
                        <button class="btn btn-default" (click)="setCurrentDate()">
                          Set Current Date
                        </button>
                    </div>
            </div>
          </div>
          <div class="col-sm-3 col-md-3 col-lg-3 col-xs-12" *ngIf="showWeeks">
          
              <div class="form-group">
                  <mat-select placeholder="Select Weeks" class="form-control" name="weeks" (ngModelChange)="weekSelect($event)">
                    <mat-option *ngFor="let count of weeks" [value]="count">
                      {{count}} Weeks
                    </mat-option>
                  </mat-select>
              </div>
          </div>
    </div>
    
  </div>
  <div class="col-sm-4 col-md-4 col-lg-4 col-xs-12" id="time-section">
   
        <div class="input-group">
            <input type="time" class="form-control" atp-time-picker [(ngModel)] = "selectedTime" (ngModelChange)="onTimeSelect($event)" id="ngx-atp-time-picker"/>
            <div class="input-group-btn">
              <button mat-raised-button class="btn btn-default" (click)="setCurrentTime()">
                  Set Current Time
              </button>
            </div>
         </div>
    
      
  </div>

</div>`,
    styles: [`#time-section{display:inline-block}#ngx-atp-time-picker,#ngx-mat-form-field{width:100%}.up{bottom:100%!important;top:auto!important}.time-btn{margin-top:-20px}`],
    providers: [
        { provide: MAT_DATE_FORMATS, useValue: MY_FORMATS },
        { provide: DateAdapter, useClass: MomentDateAdapter },
        {
            provide: NG_VALUE_ACCESSOR,
            useExisting: forwardRef(() => NgxDateTimePickerComponent),
            multi: true
        }
    ]
})
export class NgxDateTimePickerComponent implements OnInit, ControlValueAccessor {

    // public date = new FormControl(moment());
    public selectedTime = moment().format('HH:mm');
    public selectedDate = moment().format();
    public loadInitial = false;
    @Input() weeks: number[] = [0 , 2 , 4, 6, 8, 12, 16, 24];
    @Input() modelValue: any;
    @Input() showTime = false;
    @Input() showWeeks = true;
    @Output() onDateChange = new EventEmitter<any>();
    public onChange: any = () => { };
    public onTouched: any = () => { };
    public ngOnInit() {

    }

    public get value() {
        return this.modelValue;
    }

    public set value(val) {
        setTimeout(() => {
            this.onDateChange.emit(val);
        }, 100);
        this.onChange(val);
        this.onTouched();
    }

    public writeValue(value) {
        if (!this.loadInitial) {
            this.setFormValues(value);
        }
    }

    public setFormValues(val) {

        this.loadInitial = true;

        this.selectedDate = moment(val).format();
        this.selectedTime = moment(val).format('HH:mm');
        if (val instanceof Date) {
            this.value = moment(val).format();
        } else {
            this.value = val;
        }
        this.modelValue = this.value;

    }

    public registerOnChange(fn) {
        this.onChange = fn;
    }

    public registerOnTouched(fn) {
        this.onTouched = fn;
    }

    public onDateSelect($event) {
        const setDate = moment($event);
        const setTime = this.selectedTime;
        this.setDateTime(setDate, setTime);

    }
    public onTimeSelect($event) {
        const setDate = moment(this.selectedDate);
        const setTime = $event;
        this.setDateTime(setDate, setTime);
    }

    public setCurrentTime() {
        const setDate = moment(this.selectedDate);
        const currentTime = moment().format('HH:mm');
        this.setDateTime(setDate, currentTime);
    }

    public weekSelect($event) {
        const nextWeekDate = moment(this.selectedDate).add($event , 'weeks');
        const nextWeekTime = this.selectedTime;
        this.setDateTime(nextWeekDate, nextWeekTime);
    }

    public setCurrentDate() {
        const currentDay = moment();
        const currentTime = moment().format('HH:mm');
        this.setDateTime(currentDay, currentTime);


    }

    public setDateTime(setDate, setTime) {
        const newDate = moment(setDate).format('DD-MM-YYYY');
        const newTime = setTime;
        const newDateTime = moment(newDate + '' + newTime , 'DD-MM-YYYY HH:mm');
        const dateTimeString = moment(newDateTime).format();
        this.selectedDate = dateTimeString;
        this.selectedTime = newTime;
        this.modelValue = dateTimeString;
        this.value = dateTimeString;


    }
}
